import $ from 'jquery';
export function alertMessage(message){
    $.alert({
        title: "Error",
        content: message,
        type: 'red',
        typeAnimated: true,
    });
}

export function successMessage(message){
    $.alert({
        title: "Success",
        content: message,
        type: 'green',
        typeAnimated: true,
    });
}

