import React from "react";
import Footer from '../components/Footer';
import Header from "../components/Header";
import './../App.css';
import Web3 from 'web3'
import {CONTRACT_ADDRESS,ABI,NODE_URL} from './../config/super';
import {USDT_ADDRESS,USDT_ABI} from './../config/usdt';
import { toast } from 'react-toastify';
import strings from "../components/Language";
class IDO extends React.Component {
    constructor() {
        super();
        this.state={
            SuperNode:null,
            node_sold:0,
            node_price:0,
            node_oprice:0,
            is_purchased:false,
            account:null,
            total_sold:[0,0,0],
            is_registerd:false,
            paid_amount:0,
            is_connected:false,
            sponsor:"",
        }
    }

    number_format(number,decimals,dec_point,thousands_sep) {
        number  = number*1;//makes sure `number` is numeric value
        var str = number.toFixed(decimals?decimals:0).toString().split('.');
        var parts = [];
        for ( var i=str[0].length; i>0; i-=3 ) {
            parts.unshift(str[0].substring(Math.max(0,i-3),i));
        }
        str[0] = parts.join(thousands_sep?thousands_sep:',');
        return str.join(dec_point?dec_point:'.');
    }

    async componentDidMount(){
        if(window.ethereum){
            const web3 = new Web3(window.ethereum || NODE_URL);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            let total_sold=8888;
            this.setState({total_sold:"123"});
            this.setState({node_sold:1111,node_price:12345,node_oprice:678910});
            let accounts=await web3.eth.getAccounts();
            if(accounts.length!=0){
                this.setState({is_connected:true})
                this.setState({account:accounts[0]})
                
                const user = await SuperNode.methods._user(accounts[0]).call();
                console.log(user);

                if(user[3]==1){
                    this.setState({is_registerd:true});
                    this.setState({sponsor:user[1]})
                }
                if(user[6]==1){
                    this.setState({is_purchased:true});
                    this.setState({paid_amount:this.number_format((user[7][5]-user[7][3]),9,'.',',')});
                }
            }
        }
        else{
            const web3 = new Web3(NODE_URL);
            this.setState({total_sold:"123"});
            this.setState({node_sold:1111,node_price:12345,node_oprice:678910});
        }
    }

    async doApprove(){
        if(this.state.is_connected){
            let that=this;
            const web3 = new Web3(window.ethereum);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            let accounts=await web3.eth.getAccounts();
            const user = await SuperNode.methods._user(accounts[0]).call();
            console.log(user);
            const Token = new web3.eth.Contract(USDT_ABI, USDT_ADDRESS);

            if(user[3]==1){
                this.setState({is_registerd:true});
            }
            if(user[6]==1){
                this.setState({is_purchased:true});
            }
            if(this.state.is_registerd==false){
                this.alertMessage(strings.account_not_registered);
                return false;
            }
            else if (this.state.is_purchased){
                this.alertMessage(strings.already_purchase);
                return false;
            }
            else {
                const resolveAfter3Sec = new Promise((resolve, reject) =>{
                    Token.methods.approve(CONTRACT_ADDRESS,"500000000000000000000").send(
                        {
                            from: this.state.account,
                        })
                    .on('transactionHash', function(hash){
                    })
                    .on('receipt', function(receipt){
                        toast.success(strings.approved, {
                            position: "top-center",
                            autoClose: !1,
                            hideProgressBar: true,
                            closeOnClick: true,
                            pauseOnHover: false,
                            draggable: true,
                            progress: 0,
                        });
                        setTimeout(function(){
                            
                        },5000)
                        resolve(true);
                    })
                    .on('confirmation', function(confirmationNumber, receipt){
                    })
                    .on('error', function(error, receipt) {
                        //console.log('error',error,receipt);
                        that.alertMessage(error.message);
                        reject(error.message)
                    });
                });
                toast.promise(
                    resolveAfter3Sec,
                    {
                      pending: strings.processing,
                      success: strings.approved,
                      error: strings.rejected,
                    },{
                        position: "top-center",
                        autoClose: !1,
                        hideProgressBar: true,
                        closeOnClick: true,
                        pauseOnHover: false,
                        draggable: true,
                        progress: 0,
                    }
                )
            }
        }
        else{
            this.alertMessage(strings.please_connect);
        }
    }

    async doPurchaseNode(ido_price){
        if(this.state.is_connected){
            let that=this;
            const web3 = new Web3(window.ethereum);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            let accounts=await web3.eth.getAccounts();
            const user = await SuperNode.methods._user(accounts[0]).call();
            if(user[3]==1){
                this.setState({is_registerd:true});
            }
            if(user[6]==1){
                this.setState({is_purchased:true});
            }
            if(this.state.is_registerd==false){
                this.alertMessage(strings.account_not_registered);
                return false;
            }
            else if (this.state.is_purchased){
                this.alertMessage(strings.already_purchase);
                return false;
            }
            else {
                const resolveAfter3Sec = new Promise((resolve, reject) =>{
                    SuperNode.methods.buynode(ido_price).send(
                        {
                            from: this.state.account,
                        })
                    .on('transactionHash', function(hash){
                    })
                    .on('receipt', function(receipt){ 
                        setTimeout(function(){
                            window.location.reload();
                        },5000)
                        resolve(true)
                    })
                    .on('confirmation', function(confirmationNumber, receipt){
                    })
                    .on('error', function(error, receipt) {
                        that.alertMessage(error.message);
                        reject(error.message)
                    });
                });

                toast.promise(
                    resolveAfter3Sec,
                    {
                      pending: strings.processing,
                      success: strings.node_purchased,
                      error: strings.rejected,
                    },{
                        position: "top-center",
                        autoClose: !1,
                        hideProgressBar: true,
                        closeOnClick: true,
                        pauseOnHover: false,
                        draggable: true,
                        progress: 0,
                    }
                )
            }
        }
        else{
            this.alertMessage(strings.please_connect);
        }
    }

    pad(num, size) {
        num = num.toString();
        while (num.length < size) num = "0" + num;
        return num;
    }

    alertMessage(message){
        toast.error(message, {
            position: "top-center",
            autoClose: !1,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: 0,
        });
    }

    async doRegister(){
        let that=this;
        this.setState({onpress:1})
        const web3 = new Web3(window.ethereum);
        const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
        let accounts=await web3.eth.getAccounts();
        let sponsor=this.state.sponsor;
        if (!/^0x[a-fA-F0-9]{40}$/.test(sponsor)){
            this.alertMessage(strings.invalid_sponsor_address);
            this.setState({onpress:0})
            return false;
        }
        else {
            const user = await SuperNode.methods._user(accounts[0]).call();
            //console.log(user);
            if(user[3]==1){
                this.setState({is_registerd:true});
                this.alertMessage(strings.already_register);
                this.setState({onpress:0})
                return false;
            }
            else{
                const sponsor_check = await SuperNode.methods.checkSponsorUser(this.state.sponsor).call();
                //console.log(sponsor_check);
                if(sponsor_check[0]==0){
                    this.alertMessage(strings.sponsor_not_found);
                    this.setState({onpress:0})
                    return false;
                }
            }
        }
        
        const resolveAfter3Sec = new Promise((resolve, reject) =>{
            SuperNode.methods.register(this.state.sponsor).send({from: this.state.account})
            .on('transactionHash', function(hash){
               //console.log('transactionHash',hash);
            })
            .on('receipt', function(receipt){
                setTimeout(function(){
                    window.location.reload();
                },5000)
                that.setState({onpress:0})
                resolve(true)
            })
            .on('confirmation', function(confirmationNumber, receipt){
                //console.log('confirmation',confirmationNumber,receipt);
            })
            .on('error', function(error, receipt) {
                //console.log('error',error,receipt);
                that.alertMessage(error.message);
                that.setState({onpress:0})
                reject(error.message)
            });
        });

       
        toast.promise(
            resolveAfter3Sec,
            {
              pending: strings.processing,
              success: strings.registered,
              error: strings.rejected,
            },{
                position: "top-center",
                autoClose: !1,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: 0,
            }
        )
    }
    
    render() {
        return (
            <div className="">
                
                <div id="main-wrapper">
                    <Header/>
                    <div className="content-body">
                        <div className="container-fluid">
                            <div className="row mb-4">
                                <div className="col-lg-6 mx-auto mt-2 mb-2">
                                    {
                                        this.state.is_purchased==false?
                                        <div className="card">
                                            {/* <div className="row">
                                                <span className="badge dark text-white bg-secondary rounded-circle float-right"><a href={"https://bscscan.com/address/"+CONTRACT_ADDRESS} rel="noreferrer" target="_blank" className="text-white">i</a></span>
                                            </div> */}
                                            <div className="card-header border-0 d-block">
                                                <h5 className="text-dark">{strings.connected_wallet}:</h5>
                                                <h5 className="text-dark">{this.state.account}</h5>
                                                <h5 className="text-dark">{strings.sponsor_address}:</h5>
                                                <h5 className="text-dark">{this.state.sponsor}</h5>
                                                {/* <ul className="portofolio-social">
                                                    <li><a href="/">{this.state.total_sold[0]}</a></li>
                                                    <li><a href="/">{this.state.total_sold[1]}</a></li>
                                                    <li><a href="/">{this.state.total_sold[2]}</a></li>
                                                </ul> */}
                                            </div>
                                            <div className="card-body">	
                                                <div className="row">
                                                    <div className="row mx-auto text-center">
                                                        {
                                                            this.state.is_connected && !this.state.is_registerd?
                                                                <li className="nav-item">
                                                                    <button className="btn btn-rounded btn-primary d-block ml-1" data-toggle="modal" data-target="#register_modal">{strings.register}</button>
                                                                </li>
                                                            :
                                                            <>
                                                                <button onClick={()=>this.doApprove()} className="btn btn-success btn-sm alert-link">{strings.approve}</button>
                                                                <button type="button" className="btn btn-secondary mx-2" onClick={()=>this.doPurchaseNode(500)}>{strings.contribute_now} 500USDT</button>
                                                            </>
                                                        }
                                                    </div>
                                                    <div className="row mx-auto text-center">
                                                        {
                                                            this.state.is_connected && !this.state.is_registerd?
                                                            <></>
                                                            :
                                                            <>
                                                                <button type="button" className="btn btn-secondary mx-2" onClick={()=>this.doPurchaseNode(200)}>{strings.contribute_now} 200USDT</button>
                                                                <button type="button" className="btn btn-secondary mx-2" onClick={()=>this.doPurchaseNode(100)}>{strings.contribute_now} 100USDT</button>
                                                            </>
                                                        }
                                                    </div>
                                                    <div className="basic-form text-center mt-2">
                                                        <small className="text-danger text-center">{strings.node_purchase_note}</small>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    :
                                        <div className="card">
                                            {/* <div className="row">
                                                <span className="badge light text-white bg-secondary rounded-circle float-right"><a href={"https://bscscan.com/address/"+CONTRACT_ADDRESS} rel="noreferrer" target="_blank" className="text-white">i</a></span>
                                            </div> */}
                                            <div className="card-header border-0 d-block">
                                                <h5 className="text-dark">{strings.connected_wallet}:</h5>
                                                <h5 className="text-dark">{this.state.account}</h5>
                                                <h5 className="text-dark">{strings.sponsor_address}:</h5>
                                                <h5 className="text-dark">{this.state.sponsor}</h5>
                                                {/* <ul className="portofolio-social">
                                                    <li><a href="/">{this.state.total_sold[0]}</a></li>
                                                    <li><a href="/">{this.state.total_sold[1]}</a></li>
                                                    <li><a href="/">{this.state.total_sold[2]}</a></li>
                                                </ul> */}
                                            </div>
                                            <div className="card-header border-0 d-block">
                                                <h2 className="text-center text-dark">{strings.your_contribute}</h2>
                                                <ul className="portofolio-social">
                                                    <li><a href="/">0</a></li>
                                                    <li><a href="/">0</a></li>
                                                    <li><a href="/">1</a></li>
                                                </ul>
                                            </div>
                                            <div className="card-body">	
                                                <div className="row mb-2">
                                                    <div className="col-lg-12">
                                                        <h4 className="text-dark text-center">{strings.locked} USDT :  {this.state.paid_amount}</h4>
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    <div className="basic-form">
                                                    </div>
                                                    <div className="row mx-auto text-center">   
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    }
                                </div>
                            </div>	
			            </div>
                    </div>
                    <div className="modal fade" id="register_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered">
                            <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="staticBackdropLabel">{strings.register}</h5>
                            </div>
                            <div className="modal-body">
                                <div className="form-group">
                                    <label htmlFor="recipient-name" className="col-form-label">{strings.sponsor_wallet_address}:</label>
                                    <input type="text" className="form-control" id="recipient-name" onChange={(e)=>this.setState({sponsor:e.target.value})}/>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">{strings.close}</button>
                                {
                                    this.state.onpress?
                                    <button type="button" className="btn btn-primary">
                                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                        <span className="visually-hidden">&nbsp;Loading...</span>
                                    </button>:
                                    <button type="button" className="btn btn-primary" onClick={()=>this.doRegister()}>{strings.confirm}</button>
                                }
                                
                            </div>
                            </div>
                        </div>
                    </div>
                    <Footer/>
                </div>
            </div>
        );
    }
}

export default IDO;